.popover-checklist {
    margin-top: 0em;

    label {
        color: $text-subtle;
        font-size: rem(12px);
        font-weight: 700;
        line-height: 16px;
        margin-block: rem(4px);
    }

    input {
        background-color: $input-bgc;


        margin: rem(4px) 0 rem(12px);
        padding: rem(8px) rem(12px);

        border: none;
        border-radius: rem(3px);
        box-shadow: inset 0 0 0 rem(2px) var(--ds-border-input, #dfe1e6);
        line-height: rem(20px);
        outline: none;

        transition-duration: 85ms;
        transition-property: background-color,
            border-color,
            box-shadow;
        transition-timing-function: ease;

        &::placeholder {
            color: $card-title;
        }

        &:hover {
            background-color: $input-hover;
        }

        &:focus {
            background-color: #fff;
            box-shadow: inset 0 0 0 2px $btn-blue;
        }

    }

    .add-button {
        padding: rem(6px) rem(24px);
        margin: rem(8px) rem(4px) 0 0;
        background-color: $btn-blue;
        color: #fff;
        width: rem(74px);
        height: rem(32px);
    
        border-radius: rem(3px);
    
        transition: 85ms all ease;
    
        &:hover {
          background-color: $btn-blue-hover;
        }
      }
}