.popover {
  padding: 0 rem(12px) rem(12px);

  background-color: #fff;
  // filter: drop-shadow(0px 0px 10px rgba(0, 0, 0, 0.2));
  box-shadow: $card-shadow;
  border-radius: 3px;
  width: 304px;
  // width: fit-content;
  z-index: 1000;

  @include for-wide-layout {}
}

.popover-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}